/**  : */
@import 'keyframes';

/**    : */
// @import "fullscreen";

/*
* (i)    
*    *__container
*  header__container, main__container  ..
*/
[class*='__container'] {
  max-width: rem($maxWidthContainer);
  margin: 0 auto;
  padding: 0 rem($mainOffset);

  @media(max-width:1000px){
    padding: 0 rem(35);
  }
  @media(max-width:768px){
    padding: 0 rem(20);
  }
}

//    (IBG) ============================================
[class*='-ibg'] {
  position: relative;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

[class*='-ibg_contain'] {
  img {
    object-fit: contain;
  }
}


//  ===================================================================
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  /**   clip -     position: sticky */
  overflow: clip;


  // .loaded & {
  //   transform: translateY(0%);
  //   transition: all 0.6s ease-in-out  0s;
  // }
}

.page {
  position: relative;
  flex: 1 1 auto;

  [data-observ] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(2);
    background: transparent;
    pointer-events: none;
  }

  &_start {
    padding-top: rem($headerHeight);
  }
}
